import { theme } from "@chakra-ui/core";
import palx from 'palx'

import isArray from 'lodash/isArray';
import range from 'lodash/range';
import get from 'lodash/get';
import mapValues from 'lodash/mapValues';

import memorize from '../../utils/memorize'

const emToPx = em => `${em * 16}px`

export const breakpoints = [30, 48, 62, 80].map(emToPx)
export const containerWidth = [26, 44, 58, 76].map(emToPx)
export const responsiveIndex = [
  [2, 'mobile'],
  [3, 'tablet'],
]

const responsiveMap = breakpoints.map((_, i) => {
  const id = responsiveIndex.findIndex(([ri]) => ri > i)
  return id >= 0 ? id : responsiveIndex.length
})

export const responsive = memorize((...args) => {
  const argsLen = args.length
  if (argsLen <= 1) return args[0]
  return breakpoints.map((_, i) => get(args, [responsiveMap[i]], null))
});

export const mobileOrDesktop = responsive

const font = 'Arial, "PingFang TC", "HeiTi TC", "Microsoft JhengHei", sans-serif';
const fonts = {
  heading: font,
  body: font,
  mono: "Menlo, monospace",
}

const key = [50].concat(range(100, 1000, 100))
const colors = mapValues(palx('#6980DB'), colors => {
  if (isArray(colors)) {
    return colors.reduce((all, c, i) => {
      all[key[i]] = c
      return all
    }, {})
  }
  return colors
})

const darkBlue = '#4863BD'
const blue = '#6980DB'
const lightBlue = '#B7C2F4'
const paleBlue = '#EBEFFF'

const secondary = 'green'
const danger = 'red'
const bg = '#F6F7FF'
const text = '#404B55'

const heading = '#403D88'

export default {
  ...theme,
  fonts,
  colors: {
    ...theme.colors,
    ...colors,
    primary: blue,
    secondary: get(colors, `${secondary}.700`),
    danger: get(colors, `${danger}.700`),
    text,
    heading,
    bg,
    paleBlue,
    lightBlue,
    darkBlue,
  },
  breakpoints,
  containerWidth,
};
