import React from 'react';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import styled from '@emotion/styled';

import themeGet from '@styled-system/theme-get';
import Box from './Box'

const borderRadius = '0.125em'

const StyledDropdown = styled(Dropdown)`
  .Dropdown-control {
    border: none;
    border-radius: ${borderRadius};
    text-align: center;
    background-color: white;
    color: black;
    font-family: inherit;
    padding: 0.25em;
    font-size: 1em;
    cursor: pointer;
    transition: none;
    box-shadow: 0.125em 0.125em 0.125em ${themeGet('colors.blackAlpha.300')};
  }
  &.is-open {
    .Dropdown-control {
      border-radius: ${borderRadius} ${borderRadius} 0 0;
    }
  }
  .Dropdown-placeholder {

  }
  .Dropdown-menu {
    border: 1px solid;
    transform: translateY(1px);
    border-radius: 0 0 ${borderRadius} ${borderRadius};
  }
  .Dropdown-option {
    color: inherit;
    text-align: center;
    padding: 0.5em 1em;
    border-bottom: 1px solid gray;
  }
  .Dropdown-arrow {
    border-color: black transparent transparent;
    top: 0.8em;
    right: 0.5em;
  }
`

const CustomDropdown = ({
  options,
  onChange,
  onBlur,
  value,
  name,
  placeholder,
  disabled,
  ...props
}) => {
  const activeTypeOption = options && options.find((opt) => opt.value === value)
  return (
    <Box {...props}>
      <StyledDropdown
        onChange={onChange}
        options={options}
        value={activeTypeOption}
        placeholder={placeholder}
        disabled={disabled}
      />
    </Box>
  );
};

CustomDropdown.defaultProps = {
  onChange: () => {},
}

export default CustomDropdown;
