import React from 'react'

import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Heading from '../../components/Heading'
import Image from '../../components/Image'
import StatsGroup from '../../components/StatsGroup'

import envelope from './envelope.svg'

const CaseSummary = ({ data, texts, ...props }) => {
  return (
    <Box {...props}>
      <Box width={1 / 2} mx="auto" my="1em">
        <Image src={envelope} />
      </Box>
      <Box>
        <Heading.H4 fontSize="1.5em">{texts.total}</Heading.H4>
        <Flex alignItems="flex-end" my="1em">
          <Box flex="1" borderBottom="0.25em solid" borderBottomColor="white" px="0.5em">
            <Text
              color="primary"
              lineHeight="1"
              fontSize="3.25em"
              fontWeight="bold"
            >{data.summary.total.toLocaleString()}</Text>
          </Box>
          <Box ml="0.5em" fontSize="1.25em">件</Box>
        </Flex>
      </Box>
      <Box>
        <Heading.H4 fontSize="1.5em">{texts.med_total}</Heading.H4>
        <StatsGroup
          my="1em"
          values={data.summary.meds.map(({ key, value }, i) => ({
            label: texts[key],
            color: data.colors[i],
            value,
          }))}
        />
      </Box>
    </Box>
  )
}

export default CaseSummary
