import { useMemo } from 'react'
import useFetch from 'use-http'
import sortBy from 'lodash/sortBy'
import size from 'lodash/size'

import theme from '../components/ThemeProvider/theme'

const totals = [
  'med_total',
  'total',
]

const keys = [
  'med_accepted',
  'med_rejected',
  'med_pending',
]

const dataHandler = (d) => {
  const total = d.reduce((res, { value }) => {
    res = res + value
    return res
  }, 0)
  const dountData = d.map(({ label, value }) => (
    {
      y: value / total,
      x: label
    }
  ))
  return dountData
}

export default (year) => {
  const { data } = useFetch(`data?year=${year}`, null, [year]) // onMount

  return useMemo(() => {
    if (!data || !size(data)) return data
    const summary = {}
    const months = []
    const areaData = {}
    data.meds.forEach(({ month, areas }) => {
      const monthData = {}
      areas.forEach(({ stats, areaname }) => {
        areaData[areaname] = areaData[areaname] || {}
        keys.forEach((key) => {
          summary[key] = summary[key] || 0
          summary[key] += stats[key]

          monthData[key] = monthData[key] || 0
          monthData[key] += stats[key]

          areaData[areaname][key] = areaData[areaname][key] || 0
          areaData[areaname][key] += stats[key]
        })
        totals.forEach(key => {
          summary[key] = summary[key] || 0
          summary[key] += stats[key]

          areaData[areaname][key] = areaData[areaname][key] || 0
          areaData[areaname][key] += stats[key]
        })
      })
      months.push(
          keys.map(key => ({
          x: month,
          key,
          y: monthData[key],
          value: monthData[key],
        })),
      )
    })
    const areas = Object.entries(areaData).map(([label, stats]) => ({
      label,
      ...keys.reduce((all, key) => {
        all[key] = stats.med_total ? stats[key] / stats.med_total : 0
        return all
      }, {})
    }));
    return {
      areas: sortBy(areas, 'med_accepted').reverse(),
      summary: {
        total: summary.total,
        meds: keys.map(key => ({
          key,
          value: summary.med_total ? summary[key] / summary.med_total : 0,
        })),
      },
      months,
      keys,
      colors: [
        theme.colors.primary,
        theme.colors.darkBlue,
        theme.colors.lightBlue,
      ],
      channel: dataHandler(data.channel),
    }
  }, [data])
}
