import React from 'react'
import {
  VictoryChart,
  VictoryStack,
  VictoryGroup,
  VictoryBar,
  VictoryLabel,
  VictoryTooltip,
  VictoryVoronoiContainer,
} from 'victory'
import round from 'lodash/round';

// import Box from '../../components/Box'
import withChartSize from '../withChartSize'

const ColumnChart = ({ data, texts, size, ratio, isIE }) => {
  const transformData = data.areas.reduce((res, {label, med_accepted, med_pending, med_rejected}) => {
    res.forEach((dataset, i) => {
      if (!i) {
        dataset.push({ x: label, y: med_accepted * 100, key: 'med_accepted'})
      } else if (i === res.length - 1) {
        dataset.push({ x: label, y: med_pending * 100, key: 'med_pending'})
      } else {
        dataset.push({ x: label, y: med_rejected * 100, key: 'med_rejected'})
      }
    })
    return res
  }, [[], [], []])
  const colors = data.colors.map(color => [color])
  // console.log(transformData)
  return (
    <VictoryChart
      domainPadding={{ x: 50 * ratio }}
      width={size.width}
      height={300 / 800 * size.width}
      animate={{ duration: 1000 }}
      containerComponent={
        <VictoryVoronoiContainer
          style={{}}
          labels={({ datum }) => datum.y > 0 ? `${texts[datum.key]}${round(datum.y)}%` : null}
          voronoiDimension="x"
          labelComponent={<VictoryTooltip flyoutWidth={80} />}
          responsive={!isIE}
        />
      }
    >
      <VictoryGroup offset={20 * ratio} style={{ data: { width: 20 * ratio } }}>
        {transformData.map((dataset, i) => (
          <VictoryStack colorScale={colors[i]} key={i}>
            <VictoryBar
              data={dataset.slice(0, 10)}
              barWidth={20 * ratio}
            />
          </VictoryStack>
        ))}
      </VictoryGroup>
      <VictoryLabel
        y={30}
        x={24}
        text="(%)"
      />
    </VictoryChart>
  )
}

export default withChartSize(800)(ColumnChart)
