import React from 'react'
import Box from '../../components/Box'
import Heading from '../../components/Heading'
import StatsGroup from '../../components/StatsGroup'

import Donut from './Donut'

const index = ({ data, texts, ...props }) => {
  return (
    <Box {...props}>
      <Heading.H4
        mt="1rem"
        mx="1.5rem"
        fontSize="1.5em"
      >{texts.channel_title}</Heading.H4>
      <Box>
        <Donut data={data.channel} colors={data.colors} texts={texts} />
        <StatsGroup
          values={data.channel.map(({ x, y }, i) => ({
            color: data.colors[i],
            label: x,
            value: y
          })).reverse()}
          textColor="black"
          bg="transparent"
          showSep={false}
          px="2em"
          fontSize="1.125em"
          mt="-1em"
        />
      </Box>
    </Box>
  )
}

export default index
