import React from 'react'
import {
  VictoryChart,
  VictoryStack,
  VictoryAxis,
  VictoryBar,
  VictoryLabel,
  VictoryVoronoiContainer,
  VictoryTooltip,
} from 'victory'
import range from 'lodash/range';

import Box from '../../components/Box'
import withChartSize from '../withChartSize'

const Chart = ({ size: { width }, ratio, isIE, data, texts, transformData }) => (
  <VictoryChart
    height={320 / 600 * width}
    width={width}
    domainPadding={{ x: 30 * ratio }}
    animate={{ duration: 1000 }}
    containerComponent={
      <VictoryVoronoiContainer
        style={{}}
        responsive={!isIE}
        labels={({ datum }) =>
          datum.y > 0 ? `${datum.y} ${texts[datum.key]}案件` : null
        }
        labelComponent={<VictoryTooltip flyoutWidth={120} />}
      />
    }
  >
    <VictoryLabel
      y={25}
      text={texts.month_axis_label}
    />
    <VictoryStack
      colorScale={data.colors}
    >
      {transformData.map((d, i) => {
        return <VictoryBar data={d} barWidth={28 * ratio} key={i}/>;
      })}
    </VictoryStack>
    <VictoryAxis
      dependentAxis
      tickFormat={(tick) => tick}
    />
    <VictoryAxis
      tickFormat={range(12).map(month => `${month + 1}月`)}
    />
  </VictoryChart>
)

const TheChart = withChartSize(600)(Chart)

const ColumnChart = ({ data, texts }) => {
  const transformData = data.months.reduce((res, dataset) => {
    dataset.forEach((d, i) => {
      res[i].push(d)
    })
    return res
  }, [[], [], []])
  return (
    <Box borderLeft="1px solid" borderColor="lightBlue" pl="1.5em" ml="-1.5em">
      <TheChart
        data={data}
        transformData={transformData}
        texts={texts}
      />
    </Box>
  )
}

export default ColumnChart
