import React from 'react'
// import Box from '../components/Box'
import Flex from '../components/Flex'
import Text from '../components/Text'
import FullpageHeight from '../components/FullpageHeight'

import YearDropdown from './YearDropdown'

import Board from '../components/Board'
import CaseSummary from './CaseSummary'
import TypeDonut from './TypeDonut'
import PercentChart from './PercentChart'
import AreaGroupedBars from './AreaGroupedBars'
import useData from './useData'
import useTexts from './useTexts'

const HomePage = () => {
  const [year, setYear] = React.useState(new Date().getFullYear() - 1911)
  const texts = useTexts()
  const data = useData(year)
  // console.log(data)
  return (
    <FullpageHeight p="4em" minWidth={800}>
      <Flex alignItems="center" fontWeight="bold">
        <YearDropdown value={year} onChange={setYear} />
        <Text.H2 fontSize="1.75em" mx="1em">{texts.title}</Text.H2>
      </Flex>
      {data && (
        <>
          <Board as={Flex} my="2.5em">
            <CaseSummary bg="paleBlue" width="22%" p="1.5em" data={data} texts={texts} />
            <TypeDonut width="26%" data={data} texts={texts} />
            <PercentChart width="52%" data={data} texts={texts} />
          </Board>
          <Board my="2.5em">
            <AreaGroupedBars data={data} texts={texts} />
          </Board>
        </>
      )}
    </FullpageHeight>
  )
}

export default HomePage
