import React from 'react'
import { VictoryPie, VictoryContainer } from 'victory'

import Box from '../../components/Box'
import Text from '../../components/Text'

import withChartSize from '../withChartSize'

const PieChart = ({ data, colors, size, ratio, isIE }) => {
  return (
    <VictoryPie
      data={data}
      colorScale={colors}
      innerRadius={ratio * 100}
      labels={() => null}
      animate={{ duration: 1000 }}
      width={size.width}
      height={size.width}
      containerComponent={<VictoryContainer responsive={!isIE}/>}
    />
  )
}

const EnhancedPieChart = withChartSize(400)(PieChart)

const Donut = ({ data, colors, texts, ...props }) => {
  return (
    <Box position="relative" px="5%" {...props}>
      <EnhancedPieChart
        data={data}
        colors={colors}
      />
      <Box
        position="absolute"
        textAlign="center"
        top="50%"
        left="50%"
        transform="translate(-50%, -50%)"
        color="heading"
        fontWeight="bold"
      >
        <Text fontSize="1.5em">本年</Text>
        <Text>{texts.channel_label}</Text>
      </Box>
    </Box>
  )
}

export default Donut
