import React from 'react'

import Flex from './Flex'
import Text from './Text'
import Circle from './Circle'

const Icon = ({ data, colors, texts, ...props }) => (
  <Flex justifyContent="center" {...props}>
    {data.map(({ key }, i ) => (
      <Flex alignItems="center" mx="0.5em" justifyContent="center" key={i}>
        <Circle width="1em" bg={colors[i]} />
        <Text mx="0.25em" fontWeight="bold" color="text">{texts[key]}</Text>
      </Flex>
    ))}
  </Flex>
)
export default Icon
