import React from 'react'

import Flex from './Flex'
import Box from './Box'
import Text from './Text'
import Circle from './Circle'

const StatsGroup = ({ values, showSep, textColor, ...props }) => {
  return (
    <Flex bg="rgba(255,255,255,0.6)" borderRadius="1em" px="1em" py="0.5em" color="darkBlue" {...props}>
      {values.reduce((all, { label, color, value }, index) => {
        const child = (index && showSep) ? [
          <Box key={`sep-${index}`} my="0.5em" mx="0.25em" borderLeft="2px solid" borderLeftColor="lightBlue" />
        ] : []
        child.push(
          <Box key={index} flex="1" textAlign="center">
            <Flex alignItems="center" justifyContent="center">
              <Circle width="0.875em" bg={color} />
              <Text mx="0.25em" fontSize="0.875em" fontWeight="bold" color={textColor || 'text'}>{label}</Text>
            </Flex>
            <Text lineHeight="1.75" fontWeight="bold" fontSize="1.5em">{Math.round(value * 100)}%</Text>
          </Box>
        )

        return all.concat(child)
      }, [])}
    </Flex>
  )
}

StatsGroup.defaultProps = {
  showSep: true,
}

export default StatsGroup
