import useFetch from 'use-http'

const defaultTexts = {
  "title": "案件聲請與受理數據",
  "total": "受理總案件數",
  "med_total": "調解結果案件數",
  "med_accepted": "成立",
  "med_rejected": "不成立",
  "med_pending": "未結",
  "channel_title": "聲請案件類別",
  "channel_label": "總受理案類別",
  "month_title": "每月受理案件與調解結果比例",
  "month_axis_label": "總申請案件數",
  "area_title": "各區調解結果 TOP10",
  "area_list": "各區調解結果（全部）",
  "area_list_label": "案件受理狀況"
}

export default () => {
  const { data, error } = useFetch('texts', null, []) // onMount
  if (error) return defaultTexts
  return data ? Object.assign(defaultTexts, data) : {}
}
