import React from 'react'

import ColumnChart from './ColumnChart'
import Box from '../../components/Box'
import Heading from '../../components/Heading'
import Icon from '../../components/Icon'

const PercentChart = ({ data, texts, ...props }) => {
  return (
    <Box py="1em" px="1.5em" {...props}>
      <Heading.H4
        fontSize="1.5em"
      >{texts.month_title}</Heading.H4>
      <ColumnChart data={data} texts={texts} />
      <Icon data={data.months[0]} colors={data.colors} texts={texts} />
    </Box>
  )
}

export default PercentChart
