import React, { createElement } from 'react'
import { SizeMe } from 'react-sizeme'

import isIE from '../components/utils/isIE'

export default width => Comp => props => isIE ? (
  <SizeMe>
    {({ size }) => <Comp {...props} isIE size={size} ratio={size.width / width} />}
  </SizeMe>
) : createElement(Comp, { ...props, size: { width }, ratio: 1 })
