import React from 'react'
import map from 'lodash/map'

import listicon from './listicon.svg'

import GroupedBars from './GroupedBars'
import Box from '../../components/Box'
import Image from '../../components/Image'
import Flex from '../../components/Flex'
import Heading from '../../components/Heading'
import StatsGroup from '../../components/StatsGroup'
import Icon from '../../components/Icon'

const AreaGroupedBars = ({ data, texts }) => {
  return (
    <Flex>
      <Box width={7 / 10} bg="white" py="2em">
        <Flex justifyContent="space-between" alignItems="flex-end" mx="2em">
          <Heading.H4 fontSize="1.5em">{texts.area_title}</Heading.H4>
          <Icon data={data.months[0]} colors={data.colors} texts={texts} />
        </Flex>
        <GroupedBars data={data} texts={texts} />
      </Box>
      <Box width={3 / 10} height="50vh" overflowY="scroll" p="2em">
        <Flex alignItems="center">
          <Box width="3em" mr="0.25em">
            <Image src={listicon} />
          </Box>
          <Heading.H4 lineHeight="1" fontSize="1.5em">{texts.area_list}</Heading.H4>
        </Flex>
        <Box mt="1.5em">
        {data.areas.slice(10).map((dataset, k) => {
          const iconData = map(dataset, (value, key) => (
            { key, value }
          ))
          return (
            <Box key={k}>
              <Flex alignItems="flex-end">
                <Heading.H4 lineHeight="1" fontSize="1.125em" mr="0.5rem">{dataset.label}</Heading.H4>
                <Heading.H6>{texts.area_list_label}</Heading.H6>
              </Flex>
              <StatsGroup
                my="1em"
                values={iconData.slice(1).map(({ key, value }, i) => ({
                  label: texts[key],
                  color: data.colors[i],
                  value,
                }))}
              />
            </Box>
          )
        })}
        </Box>
      </Box>
    </Flex>
  )
}

export default AreaGroupedBars
